const loadImage  = imgname =>  {

    switch (imgname ) {
      case 'java':
      case 'javascript':
      case 'kotlin':
      case 'clojure':
      case 'python':
      case 'go':
      case 'sql':
      case 'git':
        return require(`../assets/${imgname}.png`);
      default:
        return require(`../assets/doc.png`);
    }
};

export default loadImage;


