import React from 'react';
import SyntaxHighlighter from 'react-syntax-highlighter';
import { docco } from 'react-syntax-highlighter/dist/esm/styles/hljs';
import { Card } from 'react-bootstrap';
import LoadImage from './LoadImage';


function Gist({id, name, description, url, files}) {

  const sources = files.map(({name, text, language}, index) => 

    <Card key={index} className="mb-2">
      <Card.Body>
        <Card.Title>
          <Card.Img variant="top" src={LoadImage(language.name.toLowerCase())} style={{width:32, height: 32}} />
          <a href={url} target="_blank" >{name}</a>
        </Card.Title>
        <div className="card-text">
          <SyntaxHighlighter language={language.name} style={docco} 
            customStyle={{fontSize: '1.0em'}}
            showLineNumbers={true}>
            {text}
          </SyntaxHighlighter>
        </div>
      </Card.Body>
    </Card>
  );

  return (
    <>
      {sources}
    </>
  );
}


export default Gist;
