import React  from 'react';
import { CardColumns, Card } from 'react-bootstrap';
import { useQuery } from '@apollo/react-hooks';
import { loader } from 'graphql.macro';
import LoadImage from './LoadImage';
const GET_REPO = loader('../graphql/repo.graphql');



function Repository() {
  const { loading, error, data } = useQuery(GET_REPO);
  if (loading) return <p>Loading..</p>
    if (error) return <p>Error!</p>

    const list  = data.viewer.repositories.edges.map(({node}) => 
    <Card key={node.id}>
      <Card.Body>
        <Card.Img variant="top" src={LoadImage('git')} style={{width:64, height:64}}  /> 
        <a href={node.url} target="_blank">{node.description||node.name}</a>
        {node.primaryLanguage && <Card.Subtitle className="text-right text-muted">{node.primaryLanguage.name} </Card.Subtitle>}
      </Card.Body>
    </Card>
    );

  return (
    <CardColumns>{list}</CardColumns>
  );

}

export default Repository;

